body {
    font-family: $base-font;
    font-size: $base-font-size;
    &::-webkit-scrollbar {
      width: 5px;
    }
    &::-webkit-scrollbar-track {
      background: transparent;
    }
    &::-webkit-scrollbar-thumb {
      background-color: $theme-color;
      border-radius: 15px;
    }
  }

  *,
  *:before,
  *:after {
    box-sizing: border-box;
  }
  #content {
    max-width: $global-width;
    margin: 0 auto;
    @media (max-width: 1600px) {
      max-width: 900px;
    }
  }

  .aos-all {
    /*width: 1000px;*/
    max-width: 98%;
    /*margin: 10vh auto 0 auto;*/
  }

  .aos-item {
    display: inline-block;
    float: left;
    width: 33.3333%;
    height: 300px;
    padding: 20px;
  }

  .aos-item__inner {
    position: relative;
    width: 100%;
    height: 100%;
    float: left;
    background: #1da4e2;
    line-height: 260px;
    text-align: center;
    color: #fff;
  }

  .container-fluid {
    @media screen and (max-width: 1024px) {
      padding-left: 15px;
      padding-right: 15px;
      padding-top: 70px;
    }
  }

  @media screen and (max-width: 800px) {
    .aos-item {
      width: 50%;
    }
  }

  /* Not Found Page Style */
  .notfound-bg {
    background: $theme-color;
    width: 100%;
    min-height: 100vh;
    text-align: center;
    .notfound-content {
      margin: 120px auto 0 auto;
      text-align: center;
      background: url(~/images/notfound-bg.svg);
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center center;
      height: 555px;
      @media (max-width: 767px) {
        height: 300px;
      }
    }
    h1 {
      display: table;
      font-size: 35px;
      font-weight: bold;
      color: #ffffff;
      text-align: center;
      max-width: 560px;
      margin: 0 auto 50px auto;
      @media (max-width: 767px) {
        font-size: 25px;
      }
      @media (max-width: 320px) {
        font-size: 20px;
      }
      @media (max-width: 280px) {
        font-size: 18px;
      }
      &.half_background {
        background: linear-gradient(to top, #7a54fc 50%, transparent 50%);
      }
    }
    p {
      font-size: 14px;
      font-weight: $base-font-weight;
      color: #ffffff;
      max-width: 430px;
      margin: 0 auto 50px auto;
      word-break: break-word;
      text-align: center;
    }
    a {
      &.homelink {
        display: inline-block;
        background: #fd8d50;
        border-radius: $border-radius;
        color: #ffffff;
        text-align: center;
        font-weight: 500;
        height: 45px;
        line-height: 45px;
        padding: 0 45px;
        margin-bottom: 50px;
      }
    }
  }
  /* payment Style Css */
  .payment-bg {
    background: $theme-color;
    width: 100%;
    min-height: 100vh;
    text-align: center;
    @media (max-width: 768px) {
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .payment-content {
      max-width: 1600px;
      margin: 0 auto;
      text-align: center;
      @media (max-width: 1600px) {
        max-width: 1030px;
      }

      img {
        max-width: 100%;
        &.imgbg {
          margin-bottom: 50px;
          @media (max-width: 768px) {
            display: none;
          }
        }
        &.imglogo {
          margin-bottom: 120px;
        }
      }
      h1 {
        display: table;
        font-size: 60px;
        font-weight: bold;
        color: #ffffff;
        text-align: center;
        max-width: 620px;
        margin: 0 auto 30px auto;

        @media (max-width: 1600px) {
          font-size: 35px;
        }
        @media (max-width: 768px) {
          font-size: 30px;
        }
        @media (max-width: 320px) {
          font-size: 25px;
        }
        @media (max-width: 280px) {
          font-size: 20px;
        }
        &.half_background {
          background: linear-gradient(to top, #ff6968 50%, transparent 50%);
        }
        &.bgpurchase {
          background: linear-gradient(to top, #ff8f4b 50%, transparent 50%);
        }
      }
      p {
        font-size: 14px;
        font-weight: $base-font-weight;
        color: #ffffff;
        max-width: 430px;
        margin: 0 auto 30px auto;
        word-break: break-word;
        text-align: center;
      }
      .support-flex {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 65px;
        i {
          font-size: 24px;
          color: #7a54fc;
          margin-right: 15px;
        }
        span {
          font-size: 14px;
          font-weight: $base-font-weight;
          color: #ffffff;
        }
      }

      a {
        &.homelink {
          display: inline-block;
          background: #fd8d50;
          border-radius: $border-radius;
          color: #ffffff;
          text-align: center;
          font-weight: 500;
          height: 50px;
          line-height: 50px;
          padding: 0 90px;
          margin-bottom: 50px;
          @media (max-width: 1600px) {
            height: 45px;
            line-height: 45px;
            padding: 0 45px;
          }
        }

        &.premiumlink {
          display: inline-block;
          background-image: linear-gradient(
            98deg,
            #be8e22 2%,
            #d8b757 50%,
            #fef1a3 83%,
            #b98517
          );
          border-radius: $border-radius;
          color: #ffffff;
          text-align: center;
          font-weight: 500;
          height: 50px;
          line-height: 50px;
          padding: 0 70px;
          margin: 100px 0;
          @media (max-width: 1600px) {
            height: 45px;
            line-height: 45px;
            padding: 0 45px;
          }
          @media (max-width: 768px) {
            margin: 50px 0;
          }
          @media (max-width: 320px) {
            padding: 0 30px;
          }
        }
      }
    }
  }
