/*Sidebar Menu  Styles */
.sidebar {
    position: fixed;
    height: 100%;
    background: $theme-color;
    width: 240px;
    padding: 0 15px;
    top: 0;
    @media (max-width: 1600px) {
      width: 230px;
    }
    @media (max-width: 1024px) {
      display: none;
    }
    a {
      &.logo {
        text-align: center;
        margin-top: 45px;
        @media (max-width: 1600px) {
          margin-top: 35px;
        }
        @media (max-width: 1280px) {
          margin-top: 15px;
        }
        img {
          max-width: 100%;
          @media (max-width: 1600px) {
            max-width: 130px;
          }
        }
      }
    }
    .sidebar-card {
      display: flex;
      align-items: center;
      padding: 0.5rem;
      .flex-item-leftside {
        flex: 0 0 30%;
        .img-container {
          width: 40px;
          height: 40px;
          border-radius: 50%;
          overflow: hidden;
          img {
            max-width: 100%;
          }
        }
      }
      .flex-item-rightside {
        flex: 0 0 70%;
        .profilep {
          font-weight: 500;
          font-size: 14px !important;
          font-stretch: normal;
          font-style: normal;
          line-height: 1.23;
          letter-spacing: normal;
          text-align: left;
          color: #ffffff !important;
          padding-left: 0.5rem;
          padding-right: 0.5rem;
          margin: 0;
        }
      }
    }
    .menu-container {
      margin-top: 45px;
      height: calc(100vh - 300px);
      overflow: auto;
      @media (max-width: 1600px) {
        margin-top: 35px;
      }
      @media (max-width: 1280px) {
        margin-top: 15px;
        height: calc(100vh - 260px);
      }
      @media (max-width: 1080px) {
        height: calc(100vh - 250px);
      }

      a {
        &.sidebar-home {
          display: flex;
          width: 100%;
          height: 55px;
          line-height: 55px;
          font-size: $base-font-size;
          font-weight: normal;
          color: #ffffff;
          border-radius: $border-radius;
          margin-bottom: 15px;
          padding: 0 15px;
          @media (max-width: 1600px) {
            height: 40px;
            line-height: 40px;
            font-size: 14px;
            padding: 0 15px;
            margin-bottom: 10px;
          }
          @media (max-width: 1280px) {
            height: 35px;
            line-height: 35px;
            font-size: 14px;
          }
          @media (max-width: 1060px) {
            height: 25px;
            line-height: 25px;
            border-radius: 8px;
            font-size: 13px;
          }
          .flex-item-leftbtn {
            padding-top: 3px;
          }
          .ti {
            font-size: 24px;
            @media (max-width: 1600px) {
              font-size: 20px;
            }
            @media (max-width: 1060px) {
              font-size: 18px;
            }
          }
          &.bgfeed {
            background-color: #7a54fc;
          }
          &.bginfo {
            background-color: #ff6968;
          }

          &.bgdisc {
            background-color: #ff8f4b;
          }

          &.bgscore {
            background-color: #b53bfe;
          }

          &.bgpremium {
            background-image: linear-gradient(
              98deg,
              #be8e22 2%,
              #d8b757 50%,
              #fef1a3 83%,
              #b98517
            );
          }

          .flex-item-leftbtn {
          }

          .flex-item-rightbtn {
            padding-left: 15px;
          }
        }
      }
      &::-webkit-scrollbar {
        width: 0;
      }
      &::-webkit-scrollbar-track {
        background: transparent;
      }
      &::-webkit-scrollbar-thumb {
        background-color: $theme-color;
        border-radius: 15px;
      }
    }
    .sidebar-bottom {
      position: absolute;
      bottom: 15px;
      left: 15px;
      right: 15px;
      .invite-card {
        border: 1px solid rgba(#718398, 0.2);
        width: 100%;
        padding: 10px;
        border-radius: $border-radius;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 15px;
        .invite-card-img {
        }
        .invite-friends {
          margin-left: auto;
          padding-left: 20px;
          h3 {
            font-size: $base-font-size;
            font-weight: 500;
            color: #ffffff;
            line-height: 1.31;
            margin-bottom: 10px;
            @media (max-width: 1600px) {
              font-size: 14px;
            }
          }
          p {
            font-size: 12px;
            color: rgba(#e9e6f3, 0.8);
            line-height: 1.33;
            margin-bottom: 0;
            max-width: 180px;
          }
        }
      }

      .premium-card {
        background: url(~/images/premium-bg.svg);
        width: 100%;
        height: 84px;
        background-size: cover;
        background-repeat: no-repeat;
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: center;

        .premium-card-img {
        }
        .premium-join {
          margin-left: auto;
          padding-left: 20px;
          h3 {
            font-size: $base-font-size;
            font-weight: 500;
            color: #ffffff;
            line-height: 1.31;
            margin-bottom: 10px;
            @media (max-width: 1600px) {
              font-size: 14px;
            }
          }
          p {
            font-size: 12px;
            color: rgba(#e9e6f3, 0.8);
            line-height: 1.33;
            margin-bottom: 0;
            max-width: 180px;
          }
        }
      }
    }
  }

