  .placeholder-text {
    width: 100%;
    margin: 20px 0;
    padding: 20px;
    text-align: center;
    background: #f3f3f3;
    border: 1px dashed #d1d1d1;
    border-radius: $border-radius;
    span {
      font-size: 24px;
      color: #7a54fc;
    }
    p {
      margin: 0;
      font-weight: normal;
    }
  }
  /*Tech Feed Page  Styles */
  .story-card {
    font-size: 0.875rem;
    border: 1px solid rgba(17, 8, 51, 0.1);
    border-radius: $border-radius;
    padding: 0;
    box-sizing: border-box;
    background-color: #ffffff;
    margin-bottom: 30px;
    height: calc(100% - 30px);
    min-height: 500px;
    position: relative;
    @media (max-width: 767px) {
      min-height: auto;
    }
    .premium-icon {
      position: absolute;
      background: $theme-color;
      top: 20px;
      left: 0;
      border-top-right-radius: 8px;
      border-bottom-right-radius: 8px;
      padding: 10px 12px;
    }
    img {
      &.imghome {
        width: 100%;
        height: auto;
        border-top-left-radius: $border-radius;
        border-top-right-radius: $border-radius;
      }
    }
    p {
      &.storyheadingp {
        font-size: $base-font-size;
        font-weight: bold;
        color: $text-dark;
        text-align: left;
        line-height: 1.79;
        padding: 15px;
        margin: 0;
        word-break: break-word;
      }
      &.storyp {
        font-size: 14px;
        line-height: 1.79;
        text-align: left;
        color: $text-p;
        padding: 0 15px 15px 15px;
        margin: 0;
        word-break: break-word;
      }
    }
    .storyflex {
      padding: 0 15px 15px 15px;
      display: flex;
      align-items: center;
      p {
        &.datep {
          color: $text-p;
          font-size: 10px;
          font-weight: 500;
          text-align: left;
          margin: 0;
          text-transform: uppercase;
          line-height: normal;
        }
      }
      .share-bookmark {
        margin-left: auto;
        display: flex;
        align-items: center;
        i {
          margin-left: 10px;
          font-size: 20px;
          cursor: pointer;
        }
        img {
          width: 20px;
          cursor: pointer;
        }
        p {
          margin: 0 10px 0 0;
          line-height: normal;
        }
      }
    }
    &:hover {
      .share-bookmark {
        img {
          animation: clapScale 0.3s infinite alternate;
        }
      }
    }
  }
  #wrapper {
    #content-wrapper {
      width: calc(100% - 240px);
      overflow-x: hidden;
      margin-left: 240px;
      @media (max-width: 1600px) {
        width: calc(100% - 230px);
        margin-left: 230px;
      }
      @media (max-width: 1024px) {
        width: 100%;
        margin-left: 0;
      }
      #content {
        max-width: $global-width;
        width: 100%;
        margin: 0 auto;
        @media (max-width: 1400px) {
          max-width: 100%;
        }
      }
      .homeheading {
        font-weight: bold;
        font-size: 32px;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.25;
        letter-spacing: normal;
        text-align: left;
        color: $text-dark;
        padding-top: 70px;
        margin-bottom: 20px;
        @media (max-width: 1600px) {
          padding-top: 50px;
        }
      }
      &.tech-feed {
        #content {
          .feed {
            display: flex;
            align-items: center;
            padding-top: 70px;
            margin-bottom: 20px;
            @media (max-width: 1600px) {
              padding-top: 50px;
            }
            .homeheading {
              padding: 0;
              margin: 0;
            }
            a {
              margin-left: auto;
              i {
                color: $text-dark;
                font-size: 20px;
              }
            }
            .profile-link {
              @media only screen and (max-width: 1024px) {
                display: none;
              }
            }
          }
          .coldiv {
            margin-top: 30px;
          }
        }
      }
    }
  }
  @keyframes clapScale {
    0% {
      transform: scale(1);
    }
    100% {
      transform: scale(1.2);
    }
  }
  /* Masonary Styles */
  /* Masonry container */
  section {
    &.masonry {
      -webkit-column-count: 3;
      -moz-column-count: 3;
      column-count: 3;
      -moz-column-gap: 20px;
      -webkit-column-gap: 20px;
      column-gap: 20px;

      @media (max-width: 768px) {
        -moz-column-count: 2;
        -webkit-column-count: 2;
        column-count: 2;
      }
      @media (max-width: 480px) {
        -moz-column-count: 1;
        -webkit-column-count: 1;
        column-count: 1;
      }
      img {
        display: inline-block;
        width: 100%;
        height: auto;
        border-radius: $border-radius;
        margin-bottom: 20px;
        cursor: pointer;
      }
    }
  }
  /*Store Item Card */
  .store-item {
    height: calc(100% - 20px);
    border: 1px solid rgba($theme-color, 0.1);
    border-radius: $border-radius;
    width: 100%;
    margin-bottom: 20px;
    background: #ffffff;
    img {
      width: 100%;
      height: auto;
      border-top-left-radius: $border-radius;
      border-top-right-radius: $border-radius;
      margin-bottom: 0;
    }
    .product {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 15px;
      .product-name {
        h4 {
