  /* InfoGraphics Page Styles */
  .info {
    .lightbox {
      position: fixed;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      display: none;
      background: #7f8c8d;
      perspective: 1000;
      z-index: 11;
      .filter {
        position: absolute;
        width: 100%;
        height: 100%;
        filter: blur(20px);
        opacity: 0.5;
        background-position: center;
        background-size: cover;
      }
      img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotateY(0deg);
        max-height: 95vh;
        max-width: calc(95vw - 100px);
        transition: 0.8s cubic-bezier(0.7, 0, 0.4, 1);
        transform-style: preserve-3d;
      }
      .closeimg {
        position: absolute;
        right: 0;
        width: 50px;
        height: 50px;
        background: rgba(0, 0, 0, 0.4);
        margin: 20px;
        cursor: pointer;
        &:after,
        &:before {
          content: "";
          position: absolute;
          top: 50%;
          left: 50%;
          width: 1px;
          height: 60%;
          background: $theme-color;
        }
        &:after {
          transform: translate(-50%, -50%) rotate(-45deg);
        }
        &:before {
          transform: translate(-50%, -50%) rotate(45deg);
        }
      }

      [class^="arrow"] {
        height: 50px;
        width: 50px;
        background: rgba(0, 0, 0, 0.4);
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        cursor: pointer;
      }

      [class^="arrow"]:after {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(-45deg);
        width: 15px;
        height: 15px;
      }

      .arrowr {
        right: 0;
      }

      .arrowr:after {
        border-right: 1px solid white;
        border-bottom: 1px solid white;
      }

      .arrowl {
        left: 0;
      }

      .arrowl:after {
        border-left: 1px solid white;
        border-top: 1px solid white;
      }
    }
  }
