          @media (max-width: 1600px) {
            max-width: 540px;
            margin-top: 170px;
          }
          @media (max-width: 1024px) {
            margin-top: 50px;
          }
          .pricing-card {
            background: #7a54fc;
            border-radius: $border-radius;
            padding: 30px;
            margin-bottom: 50px;
            @media (max-width: 1024px) {
              margin-bottom: 30px;
            }
            ul {
              list-style-type: none;
              margin: 0;
              padding: 0;
              li {
                display: block;
                color: #ffffff;
                font-size: $base-font-size;
                font-weight: normal;
                text-align: left;
                padding-left: 15px;
                margin-bottom: 20px;
                position: relative;
                @media (max-width: 1600px) {
                  font-size: 12px;
                }
                &:before {
                  content: "";
                  display: inline-block;
                  width: 3px;
                  height: 100%;
                  position: absolute;
                  top: 50%;
                  left: 0;
                  transform: translateY(-50%);
                  background: #ff8f4b;
                  border-radius: $border-radius;
                }
              }
            }
          }
          .cardh1 {
            font-size: 24px;
            font-weight: 500;
            color: #ffffff;
            text-align: left;
            margin: 0 0 30px 0;
            @media (max-width: 1600px) {
              font-size: 18px;
            }
            &.marginh1 {
              margin-top: 30px;
            }
          }

          .active-free {
            display: inline-block;
            width: 100%;
            border: 1px solid #8468e5;
            border-radius: $border-radius;
            height: 45px;
            line-height: 45px;
            font-size: $base-font-size;
            color: #9f83ff;
            text-align: center;
            margin-top: 35px;
            margin-bottom: 30px;
            cursor: pointer;
            &.premiumbg {
              margin-top: 0px;
              background: #ff8f4b;
              border: none;
              color: #fff;
            }
          }

          .premium-card {
            @extend .pricing-card;
            background: $theme-color;
            border: 1px solid rgba(#ffffff, 0.3);
            .premium-bill {
              display: flex;
              align-items: center;
              justify-content: center;
              margin: 10px 0;
              p {
                color: #ffffff;
                font-size: 14px;
                font-weight: 500;
                text-decoration: line-through;

                &.bill {
                  font-size: 24px;
                  font-weight: bold;
                  text-decoration: none;
                  padding-left: 10px;
                }
                &.bill-month {
                  font-size: 12px;
                  font-weight: bold;
                  padding-left: 10px;
                  text-decoration: none;
                }
              }
            }
            .promo-code {
              text-align: center;
              a {
                display: block;
                margin-bottom: 10px;
                color: #7a54fc;
              }
              form {
                display: none;
                .coupon-block {
                  width: 100%;
                  position: relative;
                  input {
                    width: 100%;
                    margin-bottom: 10px;
                    padding: 10px 55px 10px 20px;
                    color: #fff;
                    text-align: center;
                    background: rgba(#fff, 0.05);
                    border-radius: $border-radius;
                  }
                  button {
                    width: 30px;
                    height: 30px;
                    background: rgba(#fff, 0.03);
                    appearance: none;
                    -webkit-appearance: none;
                    -moz-appearance: none;
                    outline: none;
                    border: none;
                    color: #fff;
                    border-radius: 50%;
                    position: absolute;
                    top: 8px;
                    right: 10px;
                  }
                  p {
                    width: 100%;
                    margin-bottom: 10px;
                    font-size: 12px;
                    &.success {
                      color: $success-color;
                    }
                    &.error {
                      color: $danger-color;
                    }
                  }
                }
              }
            }
          }
        }
      }
      .modal {
        .modal-dialog {
          margin: 0 auto;
          top: 45%;
          transform: translateY(-50%);
          .modal-content {
            background: $theme-color;
            @media (max-width: 768px) {
              padding: 20px;
            }
            .modal-body {
              display: flex;
              align-items: center;
              padding: 0;
              .modal-premiumimg {
              }
              .modal-premiumcontent {
                @media (max-width: 768px) {
                  max-width: 150px;
                  margin: 0 auto;
                }
                h2 {
                  font-size: 32px;
                  font-weight: 500;
                  color: #ffffff;
                  text-align: left;
                  max-width: 215px;
                }
                p {
                  font-size: 14px;
                  font-weight: normal;
                  color: #e9e6f3;
                  max-width: 210px;
                }
                .button-premium {
                  background-image: linear-gradient(
                    98deg,
                    #be8e22 2%,
                    #d8b757 50%,
                    #fef1a3 83%,
                    #b98517
                  );
                  height: 45px;
                  line-height: 45px;
                  text-align: center;
                  color: #ffffff;
                  font-size: 14px;
                  font-weight: normal;
                  padding: 0 20px;
                  @media (max-width: 768px) {
                    width: 100%;
                    font-size: 13px;
                    padding: 0 10px;
                  }
                }
              }
            }
          }
        }
      }

      .footer {
        max-width: 460px;
        margin: 0 auto;
        text-align: center;
        margin-bottom: 100px;
        @media (max-width: 1600px) {
          margin-bottom: 50px;
        }
        a {
          display: inline-block;
          margin-bottom: 50px;
          color: #ff8f4b;
          text-decoration: underline;
          font-size: $base-font-size;
          font-weight: 500;
        }
        p {
          font-weight: normal;
          font-size: 12px;
          color: #ffffff;
        }
      }
    }
  }
