              border-radius: 6px;
              padding: 5px;
              font-size: 12px;
              position: absolute;
              z-index: 1;
              bottom: 100%;
              right: 0;
              opacity: 0;
              transition: opacity 0.3s;
              &::after {
                content: "";
                position: absolute;
                top: 100%;
                right: 20px;
                border-width: 5px;
                border-style: solid;
                border-color: #555 transparent transparent transparent;
